<template>
  <div>
    <h1>Gestió de Preguntes</h1>

    <!-- Formulari per afegir nova pregunta -->
    <div>
      <h2>Afegir Pregunta</h2>
      <input v-model="novaPregunta.pregunta" placeholder="Pregunta">
      <input v-model="novaPregunta.imatge" placeholder="URL de la imatge">
      <div>
        <input v-for="(resposta, index) in novaPregunta.respostes"
               :key="index"
               v-model="resposta.etiqueta"
               placeholder="Resposta" />
      </div>
      <input v-model="novaPregunta.resposta_correcta" placeholder="Id Resposta Correcta">
      <button @click="afegirPregunta">Afegir Pregunta</button>
    </div>


    <!-- Llista de preguntes existents -->
    <div v-for="pregunta in preguntes" :key="pregunta.id">
      <h3>{{ pregunta.pregunta }}</h3>
      <img :src="pregunta.imatge" alt="Imatge de la pregunta" width="200">
      <ul>
        <li v-for="(resposta, index) in pregunta.respostes" :key="index">
          {{ resposta.etiqueta }}
        </li>
      </ul>


      <!-- Botons per editar o eliminar -->
      <button @click="eliminarPregunta(pregunta.id)">Eliminar</button>
      <button @click="seleccionarPregunta(pregunta)">Editar</button>
    </div>


    <!-- Modal o secció per editar una pregunta -->
    <div v-if="preguntaSeleccionada">
      <h2>Editar Pregunta</h2>
      <input v-model="preguntaSeleccionada.pregunta" placeholder="Pregunta">
      <input v-model="preguntaSeleccionada.imatge" placeholder="URL de la imatge">
      <div>
        <input v-for="(resposta, index) in preguntaSeleccionada.respostes"
               :key="index"
               v-model="resposta.etiqueta"
               placeholder="Resposta" />
      </div>
      <input v-model="preguntaSeleccionada.resposta_correcta" placeholder="Id Resposta Correcta">
      <button @click="guardarEdicio">Guardar Canvis</button>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      preguntes: [], // Mantindrem les preguntes com un array
      novaPregunta: {
        pregunta: '',
        imatge: '',
        respostes: [
          { id: 1, etiqueta: '' },
          { id: 2, etiqueta: '' },
          { id: 3, etiqueta: '' },
          { id: 4, etiqueta: '' }
        ],
        resposta_correcta: null
      },
      preguntaSeleccionada: null,
    };
  },
  methods: {
    // Obtenir totes les preguntes
    async obtenirPreguntes() {
      try {
        const response = await fetch('http://a23albrobfon.dam.inspedralbes.cat:23453/preguntes');
        if (!response.ok) {
          throw new Error('Error carregant preguntes');
        }
        const data = await response.json();
        this.preguntes = data; // Assigna les preguntes carregades a l'array
        console.log('Preguntes carregades:', this.preguntes); // Debugging
      } catch (error) {
        console.error('Error obtenint les preguntes:', error);
      }
    },


    // Afegir una nova pregunta
    async afegirPregunta() {
      try {
        const response = await fetch('http://a23albrobfon.dam.inspedralbes.cat:23453/preguntes', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(this.novaPregunta)
        });


        const novaPregunta = await response.json();
        this.preguntes.push(novaPregunta); // Afegeix la nova pregunta a l'array


        // Reset del formulari
        this.novaPregunta = {
          pregunta: '',
          imatge: '',
          respostes: [
            { id: 1, etiqueta: '' },
            { id: 2, etiqueta: '' },
            { id: 3, etiqueta: '' },
            { id: 4, etiqueta: '' }
          ],
          resposta_correcta: null
        };
      } catch (error) {
        console.error('Error afegint la pregunta:', error);
      }
    },


    // Eliminar una pregunta
    async eliminarPregunta(id) {
      try {
        await fetch(`http://a23albrobfon.dam.inspedralbes.cat:23453/preguntes/${id}`, {
          method: 'DELETE',
        });
        this.preguntes = this.preguntes.filter(p => p.id !== id); // Filtra les preguntes
      } catch (error) {
        console.error('Error eliminant la pregunta:', error);
      }
    },


    // Seleccionar una pregunta per editar
    seleccionarPregunta(pregunta) {
      this.preguntaSeleccionada = { ...pregunta }; // Clona la pregunta seleccionada
    },


    // Guardar edició de pregunta
    async guardarEdicio() {
      try {
        const response = await fetch(`http://a23albrobfon.dam.inspedralbes.cat:23453/preguntes/${this.preguntaSeleccionada.id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(this.preguntaSeleccionada)
        });


        const preguntaActualitzada = await response.json();
        const index = this.preguntes.findIndex(p => p.id === this.preguntaSeleccionada.id);
        this.preguntes[index] = preguntaActualitzada;
        this.preguntaSeleccionada = null; // Reseteja la pregunta seleccionada
      } catch (error) {
        console.error('Error guardant la pregunta:', error);
      }
    }
  },
  mounted() {
    this.obtenirPreguntes(); // Obtenir les preguntes al muntar el component
  }
};
</script>
